.react-switch-checkbox {
  height: 0;
  width: 0;
  visibility: hidden;
}

.react-switch-label {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  width: 17em;
  height: 3.3em;
  margin: auto;
  background: #fff;
  border: 1px solid grey;
  border-radius: 100px;
  position: relative;
  transition: background-color 0.2s;
}

.toggleSwitchLabel {
  padding: 15px 0;
  color: #fff;
}

.react-switch-label .react-switch-button {
  position: absolute;
  /* top: 0px; */
  left: 0px;
  width: 145px;
  height: 50px;
  border-radius: 45px;
  transition: 0.2s;
  background: #f16728;
  box-shadow: 0 0 2px 0 #0a0a0a4a;
}

.react-switch-checkbox:checked + .react-switch-label .react-switch-button {
  left: calc(100% - 0px);
  transform: translateX(-100%);
}

.react-switch-label:active .react-switch-button {
  width: 60px;
}

